:root {
  --cor-azul: #00acdc;
}

body {
  box-sizing: border-box;
  background-color: #f5eded;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.main-header {
  border: 1px solid var(--cor-azul);
  background: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 40px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  max-height: 50px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.main-nav__item:not(:last-child) {
  margin-right: 20px;
}

.main-nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.main-nav__link.is-active {
  color: var(--cor-azul);
}

.main-nav__link.is-active:after {
  content: "";
  background: var(--cor-azul);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
